import React, { useState } from "react";
import styled from "styled-components";
import brandLogo from "../assets/brandLogo.png";
import { VscChromeClose } from "react-icons/vsc";
import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { changeLanguage } from "i18next";



export default function Navbar() {
  const { t } = useTranslation();
  const [active, setActive] = useState("Home");
  const [navbarState, setNavbarState] = useState(false);
  const html = document.querySelector("html");
  html.addEventListener("click", () => setNavbarState(false));

  return (
    <>
      <Nav>
        <div className="brand">
          <Link to="/"><img src={brandLogo} alt="Icon" /></Link>
          <div className="toggle">
            {navbarState ? (
              <VscChromeClose onClick={() => setNavbarState(false)} />
            ) : (
              <Link to="/menu" className="small-Menu">Menu</Link>
            )}
          </div>
        </div>
        <ul className="links">
          <li>
            <Link onClick={()=> setActive("Home")} to="/" className={active === 'Home' ? "active" : ""}>
            {t('Home')}
            </Link>
          </li>
          <li>
            <Link onClick={()=> setActive("Service")} to="/#services" className={active === 'Service' ? "active" : ""}>{t('Our Services')}</Link>
          </li>
          <li>
          <Link onClick={()=> setActive("Menu")} to="/menu" className={active === 'Menu' ? "active" : ""}>
              {t('Menu')}
            </Link>
          </li>
          <li>
            <Link onClick={()=> setActive("Footer")} to="/#footer" className={active === 'Footer' ? "active" : ""}>{t('Contact Us')}</Link>
          </li>
        </ul>
        <select onChange={(event)=> changeLanguage(event.target.value)} className="small-Menu" name="language" id="language">
          <option value="en">Eng</option>
          <option value="est">Est</option>
          <option value="rus">Rus</option>
        </select>
      </Nav>
    </>
  );
}

const Nav = styled.nav`
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 1vw;

  .brand {
    img {
      margin-top: 1rem;
      margin-left: 1rem;
      cursor: pointer;
    }
    .toggle {
      display: none;
    }
  }
  .small-Menu{
    color: #681621;
    font-weight: 600;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    transition: 0.3s ease-in-out;
  }
  .links {
    display: flex;
    list-style-type: none;
    gap: 2rem;
    margin-top:1rem;
    li {
      a {
        color: #681621;
        font-weight: 600;
        text-decoration: none;
        text-transform: uppercase;
        letter-spacing: 0.2rem;
        transition: 0.3s ease-in-out;
        &:hover {
          color: #f9c74f;
        }
      }
      
      .active {
        color: #f9c74f;
      }
    }
  }
  @media screen and (min-width: 260px) and (max-width: 1080px) {
    .brand {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      top: 0;
      .toggle {
        display: block;
      }
    }
    .links {
      display: none;
    }
  }
`;
