import React, { useState } from 'react'
import { useTranslation } from 'react-i18next';

export const Accordion = ({title, contents}) => {
  const { t } = useTranslation();
  const [isActive, setIsActive] = useState(false);


  return (
    <div className="accordion-item m-0 p-0">
        <div className={ isActive ? "accordion-title-active accordion-title": "accordion-title" } onClick={() => setIsActive(!isActive)}>
        <div className='title'>{t(title)}</div>
        <div>{isActive ? '-' : '+'}</div>
        </div>
    {isActive && <div className="accordion-content">{
        <table className='container'>
          <tbody>
            {contents.map((content,index)=> {
               return (
                <tr key={index} className='row my-1'>
                    <td className='col-1'> {content.itemCode} </td>
                    <td className='col-md-9 itemName'>{t(content.name)} {[...Array(content.spiceLevel)].map((_,i)=> <span key={i}>🌶️</span>)} <br/>
                    <span className="desc">  {t(content.description)}</span> </td>
                    <td className='col-2 text-right'>{(content.price).toFixed(2)} €</td>
                </tr>
               )})}
          </tbody>
        </table>
    }</div>}
  </div>
  )
}
